import React from "react";
import "./App.css";
import { CardList } from "./component/card-lis/cardList";
import { SearchBox } from "./component/search-box/search-box";

class App extends React.Component {
  constructor() {
    super();
    this.state = { monsters: [], serachFiled: "" };
  }

  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((response) => response.json())
      .then((users) => this.setState({ monsters: users }));
  }

  serachFileHandler = (e) => {
    this.setState({
      serachFiled: e.target.value,
    });
  };

  render() {
    const { monsters, serachFiled } = this.state;

    const monstersFliterd = monsters.filter((monster) =>
      monster.name.toLowerCase().includes(serachFiled.toLowerCase())
    );
    console.log(monstersFliterd);

    return (
      <div className="App">
        <h1 className="header">Monsters rolodex</h1>
        <SearchBox onchange={this.serachFileHandler}></SearchBox>
        <CardList monsters={monstersFliterd} />
      </div>
    );
  }
}

export default App;
